body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App {
  background-color:#6baa64;
  position:fixed;
  width:100%;
  height:100%;
  top:0px;
  left:0px;
  z-index:1000;
}

.WordleBox {
  border-style: solid;
  height: 70%;
  margin-top: 75px;
  margin-bottom: 5%;
  margin-left: 20%;
  margin-right: 20%;
  min-width: 500px;
  min-height: 400px;
  border-radius: 25px;
  background-color: #d9ded4;
}

.WordleHeader {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-weight: 1000;
  font-weight: bolder;
  height: 50px;
  border-style: solid;
  border-color: #61dafb;
  border-radius: 25px;
  font-size: x-large;
}

@font-face {
  font-family: 'Roboto';
  src: local('Roboto'), url(/wordlehelper/static/media/Roboto-Regular.f36638c2.ttf) format('truetype');
}

@font-face {
    font-family: 'Roboto';
    font-weight: 900;
    src: local('Roboto'), url(/wordlehelper/static/media/Roboto-Bold.9ece5b48.ttf) format('truetype');
}

@font-face {
    font-family: 'Roboto';
    font-weight: 900;
    src: local('Roboto'), url(/wordlehelper/static/media/Roboto-Black.301fe70f.ttf) format('truetype');
}
