.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  background-color:#6baa64;
  position:fixed;
  width:100%;
  height:100%;
  top:0px;
  left:0px;
  z-index:1000;
}

.WordleBox {
  border-style: solid;
  height: 70%;
  margin-top: 75px;
  margin-bottom: 5%;
  margin-left: 20%;
  margin-right: 20%;
  min-width: 500px;
  min-height: 400px;
  border-radius: 25px;
  background-color: #d9ded4;
}

.WordleHeader {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-weight: 1000;
  font-weight: bolder;
  height: 50px;
  border-style: solid;
  border-color: #61dafb;
  border-radius: 25px;
  font-size: x-large;
}

@font-face {
  font-family: 'Roboto';
  src: local('Roboto'), url(./fonts/Roboto/Roboto-Regular.ttf) format('truetype');
}

@font-face {
    font-family: 'Roboto';
    font-weight: 900;
    src: local('Roboto'), url(./fonts/Roboto/Roboto-Bold.ttf) format('truetype');
}

@font-face {
    font-family: 'Roboto';
    font-weight: 900;
    src: local('Roboto'), url(./fonts/Roboto/Roboto-Black.ttf) format('truetype');
}